import React from 'react';

export default function Elementos() {
  return (
    <div className="page card ">
      <h1 className="titulo">ELEMENTOS QUE CONFIGURAN LAS PEDAGOGÍAS DE LAS MÚSICAS  DE TRADICIÓN ORAL</h1>
      
      <p className="parrafo">
      En este aparte se presentan los elementos considerados como característicos de las pedagogías de las músicas de tradición oral. A pesar de que se presentan de manera separada, las dimensiones y procesos están entrelazados en el quehacer musical y pedagógico. Por esta razón, posterior a la descripción de estos elementos, se presentarán las pautas pedagógicas en las que están contenidos de manera paralela. Se invita a que en la práctica las dimensiones, procesos y líneas no se aborden de manera separada en “clases” o en “horas de” cuerpo, o de escucha, por ejemplo. Por el contrario, se espera que puedan proponerse actividades en las que se escuche desde el cuerpo y del movimiento, en actividades integradas en las que se estimule la memoria desde la imitación, o la exploración desde la oralidad, entre muchas otras posibilidades. 
      </p>
    </div>
  );
}