import React from 'react';

export default function EnsambleYMontaje() {
  return (
    <div className="page card ">
      <h2 className="subcapitulo">3.4.Ensamble y montanje</h2>

      <p className="parrafo">
      En la música tradicional la participación y la creatividad están en estrecha relación con la práctica musical porque esta constituye una de las principales formas de participación en contextos artísticos, sociales y rituales. La música permite el poder ser a través de la sonoridad. Por tanto, la participación no debe pensarse solamente como el hacer música, componerla o tocarla, también se participa escuchándola, bailándola, participando de los rituales, de los parrandos, dejándose afectar por ella y expresándola de diversas maneras. Entonces, es necesario reiterar aquí que, mientras lo académico está asociado de manera fuerte con la escritura, lo oral está relacionado más con la participación y con los saberes asociados a las identidades que se expresan musicalmente. La participación es por tanto una de las características más importantes de la oralidad. No determina, no delimita o constriñe como lo hace el lenguaje escrito, por el contrario, libera y transforma, generando procesos creativos desde la práctica, la participación y la relación constante con el contexto y sus pares.  
      </p>

      <p className="parrafo">
      La dimensión social de las prácticas artísticas es fundamental para el análisis de los procesos de transmisión y apropiación en músicas tradicionales; es allí donde suelen desarrollarse estos procesos a través de la participación en cualquiera de sus formas. Es en una estructura social determinada donde se da cada uno de estos procesos, y es a su vez esta estructura la que determina en grados diversos, las características de los procesos performativos, formativos de apropiación y transmisión de estas sonoridades.  
      </p>

      <p className="parrafo">
      Dentro de los aspectos sociales se encuentran algunas razones por las cuales la dimensión educativa, en lo relativo a las músicas populares y tradicionales, encuentran poco eco dentro de la oferta y demanda actual del contexto social colombiano, incluso desde los aspectos investigativos. La mayor parte de esta producción didáctica y pedagógica tiene un carácter utilitario, en el sentido de que están orientadas a nutrir de material sonoro y de técnicas instrumentales a cada experiencia en una forma inmediata. Esto ha llevado a un cierto pragmatismo: se oye, se transcribe, se analiza, se produce un material didáctico para la próxima clase o semana, o el arreglo para ser montado en el grupo musical. Este inmediatismo deja para un “más adelante” que casi nunca llega, el estudio a fondo y a largo plazo de las músicas populares en forma un poco más integral y articulada (Miñana, Escuelas y Experiencias Pedagógicas de Música Popular, 1991). Esta necesidad de generar productos inmediatos, que padece el medio de producción, hace que los procesos participativos a la manera como se dan en las músicas tradicionales no se desarrollen de la manera como se han dado en los contextos de las comunidades de sabedores o en los lugares de origen de cada práctica. 
      </p>

      <p className="parrafo">
      Para las músicas de tradición oral, la experiencia musical y de aprendizaje está asociada a la vida social. La vida comunitaria y cultural nutre a estas músicas aportando recursos desde los textos y poéticas, imágenes, circunstancias y fiestas. La inclusión de estos relatos en las músicas, hacen de éstas un repositorio de la memoria colectiva de un pueblo.  
      </p>

      <p className="parrafo">
          Así, las músicas no se aprenden en espacios cerrados y específicos, sino en entornos propios de la vida cotidiana de los pueblos: las fiestas, los momentos de dormir, comer, los sentimientos.  En este sentido, son músicas que se aprenden desde la vivencia y la vinculación con las comunidades. 
      </p>

      <p className="parrafo">
      La práctica colectiva, el montaje de repertorios y la conformación de ensambles son esferas de vivencia de las músicas de tradición oral en los que además del disfrute, se propician espacios comunitarios de apropiación y recreación de esta manifestación cultural. En los espacios festivos y rituales, la música, la gastronomía, las artesanías y las danzas se articulan, permitiendo a la música nutrirse de la vida cultural de las comunidades. 
      </p>

      <p className="parrafo">
      En las músicas de tradición oral se aprende de manera colectiva, se necesita la presencia de los otros para que hacer y aprender música cobre sentido. Hay momentos de práctica colectiva y otros individuales. Los colectivos son prolongados y flexibles, se toca, canta y baila con practicantes que exploran, bailan y se divierten. Se imita en tiempo real y se interactúa con pares. Son periodos valiosos en los que se agudizan facultades de escucha, observación y percepción para apropiar bases que conforman el discurso musical del género. 
      </p>

        <p className="parrafo">
        La práctica individual está presente de manera constante. En algunos casos hay un proceso de aislamiento que se dedica a la práctica individual que cualifica la ejecución. Aprendices se dedican en sus casas o en espacios privados a pulir las técnicas y formas de toque aprendido en lo colectivo. La sucesión de estas formas (individual y colectiva) garantizan la formación integral del músico que practica un sistema musical, y no solo su instrumento. 
        </p>

        <p className="parrafo">
        Para las músicas de tradición oral, el montaje es una experiencia de aprendizaje en la que se vinculan la observación, la práctica, la creatividad, la exploración, la escucha, la corporalidad. El propósito del montaje no será de panera primordial, hacer una presentación para el público. Su objetivo es principalmente propiciar una experiencia de aprendizaje colectivo que puede o no derivar en una muestra final.
        </p>

        <p className="parrafo">
        Aunque lo ideal sea abordar los formatos completos, pueden hacerse acercamientos con otras sonoridades en caso de faltar la instrumentación correspondiente. En todo caso, lo fundamental será que los estudiantes puedan asumir diferentes roles en diferentes niveles, con los participantes con los que se cuente, incluso si están en diferentes momentos de aprendizaje.  En la medida en que sea posible contar con sabedores que orienten el montaje y compartan sus experiencias con los estudiantes, será de gran valor. En caso de no contar con la presencia de sabedores, podrá hacerse uso de recursos tecnológicos como videos, audios, cartillas entre otros. 
        </p>

        <p className="parrafo">
        Para los montajes será fundamental contar con obras o piezas musicales sencillas que integren elementos característicos del formato musical desde el nivel de cada estudiante. Esto implica el diseño de estrategias pedagógicas y metodológicas que simplifiquen esquemas rítmicos, melódicos y armónicos de manera tal que se permita que la música “suene” de manera simple y orgánica mientras, progresivamente y desde la expresión, se consigue un nivel de ejecución cada vez más complejo. 
        </p>



      </div>
  );
}