import React from 'react';

export default function LaImitacion() {
  return (
    <div className="page card ">
      <h2 className="subcapitulo">2.3.La imitacion</h2>

      
    <p className="parrafo">
      En las dinámicas que surgen de la práctica de las músicas de tradición oral, la imitación es un sustento primordial para la reproducción de ideas melódicas y textuales, para la apropiación de los estilos de interpretación, para la comprensión de los formatos para producir armonías, en fin, para la asunción de los modos individuales de entender la música existentes en un determinado territorio sonoro.
    </p>

    <p className="destacados">
       Un amigo de mi papá hacía coplas, era fumigador y construyó un tambor con una caneca. Con la agrupación, salían a tocar en las fiestas del arroz para que les dieran plata. Yo también quería mi platica entonces me lancé a tocar carrasca. Yo tocaba y me daban moneditas. Uno va mirando, uno ve a la gente haciendo cosas y uno trata de imitar. 
      (Hijuelapo)
     </p>

      <p className="parrafo">
      La misma dinámica de la imitación también influye para que los estilos de ejecución, las formas de las melodías y el cancionero de música popular y tradicional trasciendan en el tiempo, haciendo patente lo que es transmitido generacionalmente y al mismo tiempo imitado.
      </p>

      <p className="parrafo">
      De otra parte, el gesto corporal del maestro y de otros practicantes es fundamental para que los aprendices logren observar e imitar, y luego apropiar. Con el fin de llevar al aprendiz a aguzar su atención, en algunas regiones el maestro le pide que “pare bolas”. Se busca que el aprendiz ponga su atención en los gestos, sonoridades, formas de toque y sentidos que le permitan identificar elementos claves en la interpretación. 
      </p>

      <p className="parrafo">
      En algunas tradiciones es posible que la imitación se apoye de recursos metodológicos, como desacelerar la velocidad o descomponer en partes los temas, para facilitar el aprendizaje. En otras tradiciones, este recurso no es utilizado. En el presente lineamiento se sugiere identificar qué necesitan los estudiantes para comprender las músicas que se les presentan. Debe tenerse en cuenta que, cuando los estudiantes son ajenos a las músicas que desean aprender, requieren procesos que les permitan comprenderlas de manera paulatina y gradual.
      </p>

      <p className="parrafo">
      En este aspecto es relevante considerar que, para quienes nacen y se desarrollan en los entornos culturales de las músicas que practican, la imitación puede resultar más sencilla. Tal como se menciona en el apartado anterior, nacer en un entorno sonoro expone a los niños a la escucha y vivencia de unos ritmos, melodías y sonoridades que les permiten acercarse a las músicas desde una experiencia sonora que está presente desde la infancia. Sin embargo, para quienes no han podido experimentar las músicas de tradición oral desde la infancia, o se acercan a músicas tradicionales ajenas a su entorno propio, el acercamiento a las mismas puede resultar más complejo. De allí que para quienes no son “nativos” de las músicas de tradición oral que se desea abordar, resulte pertinente encontrar apoyos pedagógicos, hacer procesos graduales y contextualizados. A lo largo de este documento, se ofrecen algunas pautas para hacerlo. 
      </p>

      <p className="parrafo">
       A través de la imitación, los aprendices acceden a los saberes de manera directa, desde lo que el entorno les provee. Puede darse en relación a una persona con mayor experiencia (un maestro o maestra), así como en espacios colectivos de aprendizaje, tales como festivales, parrandas, parrandos y muestras musicales variadas. 
      </p>

      <p className="parrafo">
      Además de la observación, la imitación implica la escucha atenta y, de manera fundamental, de la acción práctica, pues en sí misma, la imitación no garantiza un resultado sonoro. Es necesario identificar los aspectos que deben cualificarse, para lo cual, se requiere un ejercicio de observación y escucha de su propia sonoridad y corporalidad. En este mismo sentido, se requiere el acompañamiento de un docente que identifique los aspectos a mejorar en la imitación, con el fin de ajustarlos, de precisarlos, de afianzarlos. 
      </p>

      <p className="parrafo">
      La imitación es mucho más que un momento inicial del proceso de aprendizaje. Ésta se prolonga a lo largo del tiempo ocurriendo que, a medida que los aprendices avanzan en su proceso, la imitación puede llegar a ser más compleja: de la mano de la escucha, entonces, se reconocen detalles más sutiles de una obra o pieza musical, se evidencian los gestos interpretativos que implican acentuaciones, y en general variaciones, acciones y operaciones que, en conjunto pueden ejecutarse con mayor facilidad. 
      </p>

      <p className="parrafo">
      La imitación de una sonoridad no es la única finalidad de un proceso de aprendizaje, sin embargo, sí se considera fundamental en las músicas de tradición oral, en tanto permite aprender las particularidades de su estilo y las lógicas que le son propias. Este aspecto no es de menor importancia ya que en estas músicas tienen gran relevancia maestros y estilos que marcan hitos y particularidades que definen la historia de las mismas. Así, “tocar como” un maestro determinado, resulta ser una escuela importante para el desarrollo de destrezas auditivas e interpretativas. 
      </p>

      <p className="parrafo">
      Como se mencionó anteriormente, la imitación es un recurso fundamental para el aprendizaje de estas músicas, pero no se agota en sí misma. Para los músicos de tradición oral la creación y la improvisación son elementos de gran relevancia que no pueden ser dejados de lado. En este sentido, la repetición inteligente, aquella en que la imitación que permite modificaciones, intervenciones y transformaciones, es también uno de los puentes que comunica con el proceso creativo.
      </p>

      <p className="parrafo">
      Finalmente, es necesario señalar el uso de las onomatopeyas como recurso pedagógico fundamental en las músicas de tradición oral. Las onomatopeyas no son propiamente una forma de solfeo, ya que éste es un método de entrenamiento musical utilizado para enseñar entonación con la voz durante la lectura de una partitura. En este sentido, el solfeo busca que los estudiantes entonen mientras se recitan los nombres de las notas de la melodía, respetando las duraciones (valores rítmicos) de las notas, la indicación metronómica (tempo), y omitiendo nombrar cualquier alteración, con el fin de preservar el ritmo, mientras se marca con una mano el compás.
      </p>

      <p className="parrafo">
      La onomatopeya es un recurso pedagógico de carácter nemotécnico (que estimula la memoria) a partir del cual el maestro comunica al aprendiz unas formas de toque dadas. La onomatopeya no es arbitraria. Siempre está asociada, como recurso de imitación, a los timbres y a las duraciones que se logran en un instrumento dado. En los tambores es muy usual ya que las diferentes formas de toque y las superficies del instrumento sobre las que se opera, generan variantes tímbricas que deben expresarse en la onomatopeya para lograr crear en el aprendiz la imagen sonora del golpe o esquema rítmico dado.
      </p>

      <p className="parrafo">
      De esta manera, las onomatopeyas ayudan al aprendizaje de las bases o patrones rítmico-tímbricos básicos. El desarrollo de combinaciones más complejas se da cuando el aprendiz ya ha incorporado esas bases y ha apropiado las lógicas de su combinación en enunciados más amplios y complejos. Es decir, los esquemas onomatopéyicos son de corta duración: esquemas rítmicos, células o frases cortas.
      </p>



         </div>

  );
}